body {
	margin: 50px 0;
}

.row, .table {
	border: 1px solid #333;
	color: #333;
	padding: 10px;
}

.row > span, .table > span {
	padding: 10px;
	margin-bottom: 10px;
	display: block;
}

.col, .cell {
	padding: 10px;
	margin-bottom: 10px;
}

.col > span, .cell > span {
	display: block;
	padding: 10px;
	border: 1px solid #999;
	color: #999;
}

h2 {
	margin-bottom: 10px;
	margin-top: 50px;
	max-width: 1280px;
	margin-left: auto;
	margin-right: auto;
	width: 100%;
}
